const _hoisted_4 = /*#__PURE__*/_createStaticVNode("<h1 data-v-d3156626>&quot;Bitcoin is Responsibility-Go-Up Technology&quot; - Svetski</h1><p data-v-d3156626> A few days ago, I googled &quot;Energy Economics&quot; coz Energy runs Civilization and before the Wikipedia page had finished loading, I realised. It too must have been tainted by inflationary fiat behaviour. </p><p data-v-d3156626> I checked. Bingo, it didn&#39;t make sense. Not that fiat is to fully blame for that (true :) ) but it&#39;s little bit of dirt here and little bit of value-drain there do add up. </p><p data-v-d3156626>Now I cannot unsee fiat&#39;s fakeness.</p><p data-v-d3156626> Then more dots connected - Not just the science of how energy is related to economics, but all economic science today has been tainted by fiat. </p><p data-v-d3156626> Because &quot;high prices of assets are good&quot;, economics today is only scientific as far as aggregation of human action is concerned so as to mehanistically determine profitability. </p><p data-v-d3156626> Say future AirBnB will always profit from housing because hey, they make a listing of all housing out there. </p><p data-v-d3156626> Whatever the price, somebody lives in it. They always make that dough. By charging for that insatiable illusion of choice. As if a poor person has much of one. </p><p data-v-d3156626>If you have many followers, then you will do Ok.</p><p data-v-d3156626> But this is such nonsense. We cannot all survive on trying to have many followers because news flash, that is how to build a ponzi scheme. </p><p data-v-d3156626>So what, we deserve to starve?</p><p data-v-d3156626>And if the followers starve, who will follow the celebs?</p><p data-v-d3156626> Macroeconomics is easy. Anybody with the ability to count eggs can do it. </p><p data-v-d3156626> You do not need a high level AI to tell you that millions of followers = lots of opportunity to make lots of money. </p><p data-v-d3156626> But microeconomics&#39; mainstream theories today - you bet. Pretty nonsense from many a highly credentialed Keynesian-apologist Economics textbook. </p><p data-v-d3156626> You are better off reading the 4 hour work week. At least it is simply a dream. </p><p data-v-d3156626>And it is worse but you already know it. So you read it and laugh.</p><p data-v-d3156626>Boy is Keynesian theory pretty. It sounds so sensible.</p><p data-v-d3156626> Figure this sense - If the prices of say 2 bedroom houses are going up, it means more demand for housing which means more people lack housing. The lucky ones live in 1 room rentals. </p><p data-v-d3156626> Same analysis for all assets that put money in your pocket at a negative/life worsening expense of the rest of the world. </p><p data-v-d3156626> How about you get money into your pocket at a positive expanse i.e. because you have done something that makes life easier. Like invent a way to use fossil fuels so they are cleaner. Hmm? </p><p data-v-d3156626>Maybe I&#39;m too altruist but whatever.</p><p data-v-d3156626>These realisations made me gloomy.</p><p data-v-d3156626>Then I smiled.</p><p data-v-d3156626>Why was I gloomy?</p><p data-v-d3156626> I mean, I have been born in a time when something like Bitcoin is a REALITY. </p><p data-v-d3156626></p><p data-v-d3156626> Imagine the mental torture Hayek must have gone through. The poor man. </p><p data-v-d3156626> The days when he must have thought of a roundabout way but in vain. The common man seemed not to have a way out. </p><p data-v-d3156626>Now here it is. And people are learning. And buying sats.</p><p data-v-d3156626>The Bitcoin responsibility feels like a burden.</p><p data-v-d3156626>Yes, It is a burden.</p><p data-v-d3156626> One akin to gymming, careering, adulting. Bigger muscle, Bigger creds, Bigger mental maturity. </p><p data-v-d3156626>Bitcoin is Bigger Everybody-Can-Actually-Be-Financially-Empowered!</p><p data-v-d3156626> When one gets into the flow, I can imagine few things as rewarding as pondering the intricate mechanics of the machinery of Bitcoin. </p><p data-v-d3156626> I mean, I still have no idea what a spot ETF is, or any of the technical jargon involving forks but that is Ok. No rush. </p><p data-v-d3156626>This meal is going nowhere.</p><p data-v-d3156626>I will always adore Bitcoin.</p><p data-v-d3156626> I also have no idea how Bitcoin will peg to sunlight in a positive feedback loop (for nuclear energy is not renewable. It can run out. Boohoo). </p><p data-v-d3156626> Nobody has any idea. Not even artistic AIs like DALL-E 2 know what something like that (Bitcoin getting into its own) would look like. </p><p data-v-d3156626> But who cares. Here we are with the greatest idea on Earth and it is spreading like a wild fire of hope. </p><p data-v-d3156626> Soon, when I go to a bar or a night club, I will look at the stranger next to me and they&#39;ll look back. And we shall both be thinking &quot;do you know about Bitcoin?&quot;. I&#39;ll smile and they&#39;ll smile back. </p><p data-v-d3156626>Then I&#39;ll buy my beer and sip it in bliss.</p><p data-v-d3156626> For Bitcoin is the first way to unite every variable human action towards a unified common good(wealth), Without Forcing Anybody To Comply. </p><p data-v-d3156626>I mean, wooow.</p><p data-v-d3156626></p><p data-v-d3156626> Never before have conflicting ideas ever both been tappable except under BTC. </p><p data-v-d3156626> For under BTC, the strength of the economic predator and the strength of the economic prey are both in synergy to improve the economic jungle&#39;s dynamics and aesthetic of its dance, for all. </p><p data-v-d3156626>Evolutionary economics on steroids is Bitcoin.</p><p data-v-d3156626> It is only a matter of time indeed before everyone is welcomed to a new age, to a new age. </p>", 50)
