const _hoisted_4 = /*#__PURE__*/_createStaticVNode("<h1 data-v-366b3ef1>Kings of Bitcoin by Maali Marvin</h1><p data-v-366b3ef1> Whales are people who own so much Bitcoin their activity online can significantly affect the valuation of the coins. </p><p data-v-366b3ef1> You know, like if a whale jumped into a waterbody (splash!), the ripples would be big enough to affect millions of small fish all over. </p><p data-v-366b3ef1> The ups and downs of the waves would send the price dancing like Chris Brown. </p><p data-v-366b3ef1> Michael Saylor is one of those famous whales. He owns about 150k Bitcoin. </p><p data-v-366b3ef1>Given a limited supply of 21 million, this is about 0.5%.</p><p data-v-366b3ef1> At current pricing of $21,000 per Bitcoin (which is a historic low), Saylor is thus valued in excess of $3 billion dollars. </p><p data-v-366b3ef1> Now assume Bitcoin goes to $100k before say the next 4 years are through. Saylor will thus have $30 billion dollars worth of Bitcoin. </p><p data-v-366b3ef1>If it goes any higher, and so on and so forth.</p><p data-v-366b3ef1>So the average pleb owning sats like me might feel deflated.</p><p data-v-366b3ef1>Power is still accumulating in a few hands...</p><p data-v-366b3ef1>But is it.</p><p data-v-366b3ef1> First things first. Saylor can not increase his stock of Bitcoin without puchasing more. </p><p data-v-366b3ef1> E.g. like the Emperors and Kings of old who raided villages or the Presidents of today who raid drug cartel &quot;banks&quot;, he cannot just carry out raids and take some more Bitcoin. </p><p data-v-366b3ef1>As you must know by now, if you own your keys, you own your Bitcoin.</p><p data-v-366b3ef1> Yet to find out how exactly the Silk road guys lost their Bitcoin but my bet is they left it on the exchange. </p><p data-v-366b3ef1>Secondly,</p><p data-v-366b3ef1> To purchase more Bitcoin, Saylor cannot do it with his current stock of Bitcoin. </p><p data-v-366b3ef1> So if Bitcoin rose in value say by 10x, then it would be 10x everywhere. </p><p data-v-366b3ef1>* * *</p><p data-v-366b3ef1> Thirdly is that unlike the capitalists of today who can raise capital through issuing of stocks for fiat money that Central Banks can issue through unlimited credit, Bitcoin has no such nonsense. </p><p data-v-366b3ef1> To raise capital is a Bitcoin world, whether you are a Bitcoin whale, a government or an alien, people have to give you their Bitcoin. </p><p data-v-366b3ef1> They trust you will do something useful to make the value of the coins they still own higher, so that when they get the coins they lent you back, there has been a net profit overall in terms of increased purchasing power. </p><p data-v-366b3ef1>Now, Michael Saylor is a human being and a rich one at that.</p><p data-v-366b3ef1>He has expensive taste.</p><p data-v-366b3ef1>Still assume we were in a totally Bitcoin world.</p><p data-v-366b3ef1> To buy a nice million-dollars-worth house, Saylor would have to dig into his 150k coins. It would not be much of a dent, true, but it would be something. </p><p data-v-366b3ef1> Sooner or later, if Saylor is not earning Bitcoin through increasing the value of the currency faster than he is consuming his stock, or through being given coins by people who like what he does, he will run out. </p><p data-v-366b3ef1>Maybe not him, but his grandchildren definitely.</p><p data-v-366b3ef1> This is a truly fundamental shift in the way we see the economic power of Kings. </p><p data-v-366b3ef1>* * *</p><p data-v-366b3ef1>In the past, a King did not have to prove themselves.</p><p data-v-366b3ef1> Just a word, and they would strike fear into their subjects enough to earn free economic power. </p><p data-v-366b3ef1> With their monopoly on violence, their military might did all the talking. </p><p data-v-366b3ef1> But when money moves into cyberspace and economic power is shifted from the military-industrial complex to the information-economic-value complex, big-time commando skills and lots of bullets will fall out of favour. </p><p data-v-366b3ef1> As the world gets more networked, connected and as we play more intellectual battles than ever before, the new Kings of Bitcoin will be people who know how to inspire people to build great value, people who know how to create energy efficiency, educational effectiveness, name it. </p><p data-v-366b3ef1> It is also the future of government itself. Government officials and Corporate Whales will have to provide more value or phase out naturally, let in the new kids on the block. </p><p data-v-366b3ef1> In this future, the queen is the woman who walks amongst her people and they do not envy her diamond ring which they have never owned. </p><p data-v-366b3ef1> They envy her suave intellect, her graceful way of handling disputes. Her wise King who always keeps Bitcoin going up and when he foresees a recession, he ensures enough commodities are stocked (from his own coins) for society to survive the economic winter without any children dying of the cold. </p><p data-v-366b3ef1>This is the future of money and you and I are the pioneers.</p><p data-v-366b3ef1> Whether they are remembered or not, in the long chain of causality, when a Bitcoin world blooms, plebs of today shall be the foundation stones on which it has been built. </p><p data-v-366b3ef1>* * *</p><h4 data-v-366b3ef1>Conclusion:</h4><p data-v-366b3ef1> Let us both stop envying Michael Saylor. Thinking he is unfairly rich. </p><p data-v-366b3ef1> His great work at microstrategy is what makes him great. Not his Bitcoin balance. </p><p data-v-366b3ef1>That is simply a reflection of the man&#39;s greatness.</p><p data-v-366b3ef1> Indeed, in the far future, every man will be as great as his Bitcoin balance. </p>", 47)
