const _hoisted_4 = /*#__PURE__*/_createStaticVNode("<h1 style=\"text-align:center;\" data-v-a8eb89ba> The Mathematics of Bitcoin – A Tale of Ants and Difficulty Adjustment </h1><p data-v-a8eb89ba> Being a science buff of sorts, I could not put off writing a technical piece on Bitcoin forever. </p><p data-v-a8eb89ba> Bitcoin is heavy on mathematics and I know a little bit of mathematics so, here goes based on what I understand from difficulty adjustment so far. </p><p style=\"text-align:center;\" data-v-a8eb89ba>* * *</p><p data-v-a8eb89ba>Once upon a time…</p><p data-v-a8eb89ba>Assume (the proper way to formally begin a mathematical idea);</p><p data-v-a8eb89ba>1) We are in an alternate reality in which Ants are the apex species.</p><p data-v-a8eb89ba> 2) The ants invent Bitcoin, and just like us, there are few Bitcoin miners at the beginning. </p><p data-v-a8eb89ba> 3) However, these ants invent Bitcoin as a mental exercise of sorts. Not with digital computers. They have “mental computers”. They are mentats! </p><p data-v-a8eb89ba> 4) A Bitcoin hash is initially decided by their Bitcoin proof-of-work system to be only 10 alphanumerical characters long while beginning with 9 zeros. </p><p data-v-a8eb89ba> Now assume again this (below) is our Bitcoin hash for which an Ant-miner will be rewarded some Bitcoin; </p><p data-v-a8eb89ba>000000000z</p><p data-v-a8eb89ba> Assume (last assumption. Number 5) that our ants can only count starting from 0 to 9 and then move on to letters by only saying letters from a to z. In other words, their mental computer memories are 1 letter or 1 number at a time but not both. </p><p data-v-a8eb89ba> We know that there are only 22 letters of the alphabet (a b c d e f g h i j k l m n o p q r s t u v w x y z) so we know that an ant will count (also called “mining for Bitcoin”) by going; </p><p data-v-a8eb89ba>‘0000000000’ – This will uses 1 joule of the ant’s energy.</p><p data-v-a8eb89ba>0000000001 – Uses another 1 joule of energy.</p><p data-v-a8eb89ba>…</p><p data-v-a8eb89ba>000000000a – Uses another 1 joule of energy.</p><p data-v-a8eb89ba>000000000b – Uses another 1 joule of energy.</p><p data-v-a8eb89ba>000000000c – Uses another 1 joule of energy.</p><p data-v-a8eb89ba>….</p><p data-v-a8eb89ba>000000000z – Uses another 1 joule of energy</p><p data-v-a8eb89ba> Total energy used (the difficulty of the system) = 32 joules/difficulty points (22 letters plus 10 digit-numbers). Our Satoshi Nakamoto ant is damned tired. </p><p data-v-a8eb89ba>That was a nice day’s work.</p><p data-v-a8eb89ba>It was only competing with one other ant. Call it Faketoshi ant.</p><p data-v-a8eb89ba>Well, nice race.</p><br data-v-a8eb89ba><p style=\"text-align:center;\" data-v-a8eb89ba>* * *</p><p data-v-a8eb89ba> Only 13 days later, like ants drawn to nutritious sugar, meat and vegetables, 10 other ants (warrior ants!) with tough muscles that can easily beat a difficulty of 100, have joined the party. </p><p data-v-a8eb89ba> But the Satoshi Nakamato ant (now retired) had designed a very good proof-of-work system so the system readjusts the difficulty. It now wants up to 8 zeros with the last alphanumeric characters as, you guessed it, zz. </p><p data-v-a8eb89ba> Note on how it gives incentivising rewards: Total prize is fixed and will only be shared out as per percentage level of difficulty completed, divided by all participants. </p><p data-v-a8eb89ba>(Nitty gritty Ant-Bitcoin Details)</p><p data-v-a8eb89ba> Bottom line: The system only gives away 1 BTC every 10 minutes. Like on our Earth. </p><br data-v-a8eb89ba><p data-v-a8eb89ba>So, what is the new difficulty?</p><p data-v-a8eb89ba>Target : 00000000zz</p><p data-v-a8eb89ba>Start at: 0000000000</p><p data-v-a8eb89ba>…</p><p data-v-a8eb89ba>End at : 00000000zz</p><p data-v-a8eb89ba>Total permutations = 32 times 32 = 704 times</p><p data-v-a8eb89ba> Well, none of the Level 100 difficulty-certified guys can do it that’s for sure. But, if they can find a way to do it in series (Ant-version of “parallel brute force”), they’d win! </p><p data-v-a8eb89ba> You know, if some of them could out in their 100 joules of work, then take a mental break from counting and let the others start from there and continue, it just might work. </p><p data-v-a8eb89ba>Only 8 guys need do, right?</p><p data-v-a8eb89ba>Right.</p><p data-v-a8eb89ba> Alas, no 8 warrior ants (who are the most cooperative) want to collaborate on this idea of their own accord. Too risky to loss of personal wealth i.e. counting strength, time, name, food energy eaten, name it. </p><p data-v-a8eb89ba> Enter the billionaire Queen. With the money (food) to finance big mining servers (lots of servant ants), they definitely mine the big Bitcoin. </p><p data-v-a8eb89ba> Of course, everybody else gets a share, as per the fair judgement of the systems rewarding mechanism. But, as it was in the beginning, only those who put in the work get the prize. </p><p data-v-a8eb89ba> Inflation, copying, hacking is not allowed as per the unique Ant-Bitcoin-Mind-DNA. </p><p data-v-a8eb89ba>It is impossible.</p><p data-v-a8eb89ba>-----------------</p><p data-v-a8eb89ba> Slowly, their ability to calculate really huge numbers, really fast, grows exponentially as the ants evolve. </p><p data-v-a8eb89ba> They soon become gods. And they still compete to proof the work of their Ant Bitcoin. And yes, they can’t believe they made it this far on counting damned hashes. </p><p data-v-a8eb89ba> One day, they make Ant-movies on big, soft and really smart creatures bipedal that rely on machines to do their counting for them. </p>", 53)
