 truly something” I finished for him.</p><p data-v-ad211456>Knock on the door.</p><p data-v-ad211456>It was Leah.</p><p data-v-ad211456>“Started without me?”</p><p data-v-ad211456> “You just missed my false start” I offered myself. Before John made fun of me. </p><p data-v-ad211456>Leah took my drink and sipped it.</p><p data-v-ad211456>Wow. She too acknowledged.</p><p data-v-ad211456> “Your angle of billionaires just needs some work Matt. Otherwise mine is this. </p><p data-v-ad211456> The government should love this Bitcoin thing to keep going sky high”. </p><p data-v-ad211456>“Why”, whined John.</p><p data-v-ad211456> “So we can steal all that hard earned crypto dough. We have spies everywhere, remember? </p><p data-v-ad211456>Especially in our smart phones”.</p><p data-v-ad211456>I was sober. John looked at her and smiled.</p><p data-v-ad211456>“You’re a bad girl”.</p><p data-v-ad211456>“Thank you”.</p><p data-v-ad211456>We all smiled.</p><p data-v-ad211456>“Let’s go pitch that plan to the President of the United States”.</p><p data-v-ad211456>We stood up to go.</p><p data-v-ad211456>John helped Leah put on her coat. Then she did my tie.</p><p data-v-ad211456>My idea then fully formed.</p><p data-v-ad211456>“Before we go, I just realised something”.</p><p data-v-ad211456>“What is it?” they both wheeled around.</p><p data-v-ad211456>“The billionaires must already be spying on it?!”</p><p data-v-ad211456>“You sure?”</p><p data-v-ad211456>“Yes! That is why it is rocketing up like this!”</p><p data-v-ad211456> “Well, that means the government will be late to the party” John added nonchalantly. </p><p data-v-ad211456> Leah froze. What the hell were we going to tell the President of the United States now. </p><p data-v-ad211456></p><p data-v-ad211456>-----</p><p data-v-ad211456></p><p data-v-ad211456> Bitcoin had started as an experiment in giving financial freedom to individuals without government intervention. </p><p data-v-ad211456> A gold-like digital token. Uncopiable, 100% tradeable. Only needs electricity and an internet connection to be used as money. And there is no boss. </p><p data-v-ad211456>Then people actually believed in it and it boomed.</p><p data-v-ad211456> Then governments found out about it. And minded their business as usual. </p><p data-v-ad211456>Pumping credit and oil.</p><p data-v-ad211456> Then billionaires noticed that more people were noticing it and started stacking their wealth in it. </p><p data-v-ad211456>More people found out about that, so they trusted it some more.</p><p data-v-ad211456>It grew.</p><p data-v-ad211456> We also played our cards all right, but – swallows – seems the billionaires are now logging user-data on it. </p><p data-v-ad211456>As we all know, ain’t no billionaire ever kept data on shi…</p><p data-v-ad211456>“Wait what?”</p><p data-v-ad211456>“Sorry, Mr. President?”</p><p data-v-ad211456>“You just said the billionaires are logging user-data on it?”</p><p data-v-ad211456>“Yessir”</p><p data-v-ad211456>“Where is the data?”</p><p data-v-ad211456>“Michael. Open It”</p><p data-v-ad211456> Hundreds of thousands of data files from whodunit servers scrambled for space on the massive viewing screen. </p><p data-v-ad211456> They looked mostly encrypted as government tax returns, government this, that.gov records. </p><p data-v-ad211456>“How much do we have?”</p><p data-v-ad211456>“A few hundred gigabytes. But this was all from this morning Sir”.</p><p data-v-ad211456>“Jesus”</p><p data-v-ad211456>He sat back. This meant only one thing, backlash.</p><p data-v-ad211456>Loss of control. Loss of the popular vote.</p><p data-v-ad211456>It could never go out to the media hyenas.</p><p data-v-ad211456>“Did you call on them before you raided that corporate building?”</p><p data-v-ad211456> “No. We went on a hunch. Lucky, as the data was a few hours from a wipeout”. </p><p data-v-ad211456>“This is just the tip of the iceberg” finished CIA Chief AA.</p><p data-v-ad211456>“Those double crossing bastards! We bailed them out!!”</p><p data-v-ad211456> Usually, billionaires work with governments. However, billionaires blackmailing governments by just minding their own greed was too much. </p><p data-v-ad211456>“Do we know who it was?”</p><p data-v-ad211456> “Unfortunately, all traces on these lead back to a cryptographic identity” </p><p data-v-ad211456>To be continued</p>", 89)
