const _hoisted_4 = /*#__PURE__*/_createStaticVNode("<h1 data-v-58d7d7e8>Monetary Theory on Steroids by Maali Marvin</h1><p data-v-58d7d7e8> Satoshi Nakamoto will be remembered as the greatest economist who ever lived. </p><p data-v-58d7d7e8> A software developer, scientist, game theorist, mathematician, philosopher. </p><p data-v-58d7d7e8>The Magna Economist.</p><p data-v-58d7d7e8> Especially with regard to the impact of his creation, Bitcoin, on Monetary Theory. </p><p data-v-58d7d7e8>Monetary Theory deals with how to manage money in an economy.</p><p data-v-58d7d7e8> Unlike Keynesian Theory that advocates for spurring economic growth by making more people money hungry through widening the gap between the rich and the poor, through printing of risk free credit and giving it to the rich, monetary theory has an alternative idea. </p><p data-v-58d7d7e8>It advocates for controlling the money supply through taxation.</p><p data-v-58d7d7e8> If the economy is not picking up speed, then taxes should be reduced so that people use the excess cash to spend on goods in the market. And vice versa. This can be captured in the all famous Laffer curve. </p><p data-v-58d7d7e8> Now Bitcoin seems to be against taxation but it is only because the current economic systems are flawed. Foolish. </p><p data-v-58d7d7e8> Taxes, if used well, serve better than Charitable giving. Because they remind everybody to care for the whole. And I am yet to see people hate Charitable giving. </p><p data-v-58d7d7e8> What Bitcoin does is it advocates for Monetary Theory while fighting Keynesian theory. So in one fell stroke, Satoshi Nakamoto made Monetary Theory way more powerful than before. </p><p data-v-58d7d7e8> Bitcoin fights Keynesian theory because if people increasingly use a different form of money to store economic value, then printed fiat money will not strike their fancy as much and nobody will sell their assets to the rich (who just got some free cash) for fiat dollars. </p><p data-v-58d7d7e8> So the idea of the rich getting unfairly richer because they have friends in The House will fall out of favour more and more and eventually, the growing gap between the haves and have nots will no longer be allowed to grow. </p><p data-v-58d7d7e8> When this happens, then economics will finally peg to sound scientific principles such as &quot;when resources needed to live a quality life get cheaper on aggregate, it will be good for everybody&quot;. </p><p data-v-58d7d7e8> Today, under Keynesianism, a cheaper but good house is impossible. A good house must be more and more expensive. </p><p data-v-58d7d7e8>Because we are all like street children,</p><p data-v-58d7d7e8> wishing to live in the big beautiful house, with a big beautiful family </p><p data-v-58d7d7e8>but in vain.</p><p data-v-58d7d7e8>Our desire is tapped for economic gain.</p><p data-v-58d7d7e8>By making us rave with desire</p><p data-v-58d7d7e8>Run up and down</p><p data-v-58d7d7e8>Fly our cars, Fight our brother, Beat our Sister</p><p data-v-58d7d7e8>CRASH, we kill one another.</p><p data-v-58d7d7e8>All in pursuit of dollars.</p><p data-v-58d7d7e8>Oh, who knew that</p><p data-v-58d7d7e8>the pain</p><p data-v-58d7d7e8>Of longing for what we cannot attain</p><p data-v-58d7d7e8>can be tapped, like oil</p><p data-v-58d7d7e8>Will be tapped, our toil</p><p data-v-58d7d7e8>Will be stolen.</p><p data-v-58d7d7e8>To enrich those who already have more than they know</p><p data-v-58d7d7e8>To enrich those who, like us, fear the bottom</p><p data-v-58d7d7e8>Fear the blossom of Mother Nature</p><p data-v-58d7d7e8>For the ground can</p><p data-v-58d7d7e8>No longer</p><p data-v-58d7d7e8>Support anybody of its own accord</p><p data-v-58d7d7e8> Unless they are part of the Great Big Economic Machine. A failing machine </p><p data-v-58d7d7e8>Barely holding it together</p>", 39)
