const _hoisted_4 = /*#__PURE__*/_createStaticVNode("<h1 data-v-4e573d02>The Proof-of-Work Train</h1><h3 data-v-4e573d02>by Maali Marvin</h3><p data-v-4e573d02>Let us look at Bitcoin in a new way.</p><p data-v-4e573d02>Imagine it is train.</p><p data-v-4e573d02>Close your eyes. Imagine it.</p><p data-v-4e573d02>A Magnificient Express.</p><p data-v-4e573d02>Woo Woooooo !!</p><p data-v-4e573d02> Emblazoned on it&#39;s side are the words “Energy is the ability to do Work”. You step forward, grab ahold of the railing and climb abode. </p><p data-v-4e573d02> Just as it starts to chug chug out of Newbie Station. Towards grander horizons. </p><p data-v-4e573d02>You take your seat.</p><p data-v-4e573d02>&quot;Hi&quot;</p><p data-v-4e573d02>Would you look at that. You sat near a girl!</p><p data-v-4e573d02>&quot;Hi&quot;.</p><p data-v-4e573d02>She is shy. Thinks she got no business talking to a certified orange.</p><p data-v-4e573d02>&quot;I&#39;m Me&quot;.</p><p data-v-4e573d02>&quot;Nice to meet you&quot; she accepts your handshake.</p><p data-v-4e573d02> You look out the window and you see them. The terrible storm clouds being raised by The Naysayers. </p><p data-v-4e573d02> You feel the foreboarding. Can&#39;t jump off the train though, you&#39;ll crash your body worse than a Terra Luna token. </p><p data-v-4e573d02> Ghostly and forbidding, you wonder if the train will make it through them. </p><p data-v-4e573d02>&quot;Hmm&quot;.</p><p data-v-4e573d02> &quot;What?&quot; the girl still has her eyes on you. She almost worships you with those eyes. </p><p data-v-4e573d02> It&#39;s like she is searching for something. Searching for a sign of silly boyishness and not the manly manliness she sees. </p><p data-v-4e573d02>&quot;Let me come back&quot;.</p><p data-v-4e573d02>You go to the front of the train.</p><p data-v-4e573d02>To the engine room. The MV (Miner - Verifier) carriage.</p><p data-v-4e573d02>You go in. You have to be sure the train will make it.</p><p data-v-4e573d02> The heat in here is incredible. And the noise. People are arguing about block sizes and Segwit and ESG. Basically a hot marketplace of ideas. </p><p data-v-4e573d02>All the engineers (Miners and Verifiers) are standing.</p><p data-v-4e573d02>Your buddy comes along.</p><p data-v-4e573d02>&quot;HEY MAN!&quot; he shouts over the din</p><p data-v-4e573d02>&quot;DUDE!&quot; You shout back</p><p data-v-4e573d02> &quot;Welcome welcome&quot;. he seemingly mumbles and he leads you to a quite corner where you can hash all this stuff out. </p><p data-v-4e573d02>&quot;Hey, you seen the clouds??&quot;</p><p data-v-4e573d02>&quot;Yeah. He replies&quot;. Slightly worried but he smiles it off.</p><p data-v-4e573d02>&quot;Well, will the train make it?&quot;</p><p data-v-4e573d02>&quot;I have no idea&quot;.</p><p data-v-4e573d02>You look at him. Why is he so calm. He has you baffled.</p><p data-v-4e573d02>&quot;Are you having a good time?&quot;.</p><p data-v-4e573d02>&quot;Yeah&quot;.</p><p data-v-4e573d02> &quot;No like seriously, are you? Coz this is the coolest ride ever invented yet. I mean look at this thing! </p><p data-v-4e573d02>It is fantastic!&quot;</p><p data-v-4e573d02>Guy knows how to get you going. Man you love this guy.</p><p data-v-4e573d02> You imagine the scene in the Engine room. Again. And you realize, everywhere is the Engine room. Even You, where you are now, your work powers the train. </p><p data-v-4e573d02>That was the Main engine room but the whole thing is an Engine!!</p><p data-v-4e573d02>You are all riding on a pure engine.</p><p data-v-4e573d02>You all matter.</p><p data-v-4e573d02> &quot;Why did you call that place the Miners and Verifiers room the Engine room?&quot; </p><p data-v-4e573d02> &quot;Aaagh. We are just in the lead coz we got lucky but all your work inside this train powers it. Actually does more than we could ever pretend to do&quot;. </p><p data-v-4e573d02>You nod.</p><p data-v-4e573d02> The fact that this place is Honestly Exciting because of the Opportunities it keeps unfolding, you realize, is why millions of people keep climbing aboard. </p><p data-v-4e573d02> And it has space for them all. Comfy space on its rollercoaster journey. </p><p data-v-4e573d02> Right Now, it spins as much fun as learning the words of an alphabet. But soon, the grand story of the hero&#39;s journey nears. </p><p data-v-4e573d02>&quot;Oh Yeah&quot;. You smile.</p><p data-v-4e573d02>* * *</p><p data-v-4e573d02></p><p data-v-4e573d02>Minutes later.</p><p data-v-4e573d02>Emboldened.</p><p data-v-4e573d02>You are back to your seat.</p><p data-v-4e573d02> The Naysayers, you realize, ain&#39;t going to do nothing about your work. </p><p data-v-4e573d02> For here you are, putting in that work and protecting it with an energetic asset. Backing it up. </p><p data-v-4e573d02>Proofing it over and over again.</p><p data-v-4e573d02>Over and over and ...</p><p data-v-4e573d02>&quot;What are you thinking?&quot; She asks.</p><p data-v-4e573d02> &quot;I was thinking&quot;, you reply, getting closer, &quot;that it is really going to rain bulls and bears&quot;. </p><p data-v-4e573d02>She is breathing faster, desire in her eyes. &quot;SO?&quot;</p><p data-v-4e573d02> &quot;So&quot;, you respond while laying back in your seat. &quot;It is going to get really cold&quot;. </p><p data-v-4e573d02>She raises her eyebrows.</p><p data-v-4e573d02>&quot;Well, it&#39;s a good thing we got each other&quot; she winks.</p><p data-v-4e573d02>Your heart jumps. Jesus.</p><p data-v-4e573d02>You&#39;re so going to love this Proof of Work train.</p><p data-v-4e573d02></p><p data-v-4e573d02>* * *</p><p data-v-4e573d02></p><p data-v-4e573d02> We zoom out and up, and as we do so, we can see that around the train is a sort of invisible shield. </p><p data-v-4e573d02>Outside the shield, the storm clouds aren&#39;t clouds at all.</p><p data-v-4e573d02> They are all sorts of bombs, bullets, knives, cats and dogs being thrown at it. All sorts of dark spells from dark wizards, trying to destroy our Dumbledore-Potter-All the Cool Wizards combined train. </p><p data-v-4e573d02>I mean all sorts of killer looking shit.</p><p data-v-4e573d02>But it chugs along. Unscathed.</p><p data-v-4e573d02> Strengthened by each hit it survives. And the love that blossoms within. </p><p data-v-4e573d02>A benevolent beast on a mission.</p><p data-v-4e573d02>To reach grander horizons for all humankind.</p>", 81)
