const _hoisted_4 = /*#__PURE__*/_createStaticVNode("<h1 style=\"text-align:center;\" data-v-5fc4d5ca> The Untold Wonders of Deflationary Economics </h1><img alt=\"Portal depcition\" src=\"" + _imports_0 + "\" class=\"center-fit\" data-v-5fc4d5ca><p style=\"text-align:center;\" data-v-5fc4d5ca>* * *</p><p data-v-5fc4d5ca> In the beginning, God created Man and Woman. And two Economic Systems. </p><p data-v-5fc4d5ca> The Inflationary System (IS) that rides on the power behind making the starving majority envy the ultra rich, and the Deflationary System (DS) that rides on the power behind making the necessities of life cheaper so that, like Elon Musk, many more can have babies with neither worry nor sorry. And enjoy the love and joy of a big family with true financial security. </p><p data-v-5fc4d5ca>Security not from thieves, but the harshness of nature.</p><p data-v-5fc4d5ca> Here are examples of the differences between how the two systems see the world. </p><br data-v-5fc4d5ca><p data-v-5fc4d5ca><strong data-v-5fc4d5ca>1. Housing</strong></p><br data-v-5fc4d5ca><p data-v-5fc4d5ca>If the price of the average house rockets up;</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> Congrats. The homeowner just registered increased value on their house. As so many houses of its type exist, people must be getting richer. Wow! </p><br data-v-5fc4d5ca><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> Oh noo. Money was printed and given to rich people as interest free credit. </p><p data-v-5fc4d5ca> Now they are buying up all the homes in a rush which is pushing up prices. </p><p data-v-5fc4d5ca>Where will the poor commoner sleep?</p><p data-v-5fc4d5ca>They will be enslaved to rent forever!</p><br data-v-5fc4d5ca><p data-v-5fc4d5ca><strong data-v-5fc4d5ca>2. Smartphones</strong></p><br data-v-5fc4d5ca><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> Smartphones are getting cheaper by the year. We need to keep them expensive by making them fail quickly via planned obsolescence. </p><p data-v-5fc4d5ca>Coz where shall companies get money if a product lasts forever?</p><p data-v-5fc4d5ca>Indeed there is a thing as too much innovation.</p><br data-v-5fc4d5ca><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> Yes, more utility in a smart phone and it is cheaper!</p><p data-v-5fc4d5ca>Yeah baby.</p><p data-v-5fc4d5ca> The companies whose business model is to sell great computers and smartphones need to level up now. </p><p data-v-5fc4d5ca>Selling hardware is so outmoded.</p><p data-v-5fc4d5ca> Whoever realises that the money is now in customer information as they use digital products, hence leading to more effective targeted advertising, is going to run this joint. </p><p data-v-5fc4d5ca> Wait, Google has done this. Apple, Microsoft, YouTube. AirBnB, Amazon, Alibaba. </p><p data-v-5fc4d5ca>Free things. But not really free. Very Interesting don&#39;t you think.</p><p data-v-5fc4d5ca>Very exciting too.</p><br data-v-5fc4d5ca><p data-v-5fc4d5ca><strong data-v-5fc4d5ca>3. Land</strong></p><br data-v-5fc4d5ca><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> It&#39;s obvious, more human beings = increasing prices for land. </p><p data-v-5fc4d5ca>Coz the Earth is finite. Deal with it.</p><p data-v-5fc4d5ca>Guess Malthus was right all along. There are too many babies on here.</p><p data-v-5fc4d5ca> That majority won&#39;t be able to afford their own slice on earth is just unfortunate. </p><p data-v-5fc4d5ca>Of course, the rich shall inherit the Earth.</p><p data-v-5fc4d5ca>It is evolution. Isn&#39;t that what the DS believers say.</p><p data-v-5fc4d5ca>The apex predator rules the jungle.</p><br data-v-5fc4d5ca><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> Any country with cheap energy sources without plans on how to get some skyscrapers going is really not thinking. They are frozen in the past. </p><p data-v-5fc4d5ca> 10 floors on a building = 10 times the land area, which makes land cheaper if we are thinking floor space, but now there is also an increase in utility. </p><p data-v-5fc4d5ca>Remember, Cheaper but More Utility is our motto.</p><p data-v-5fc4d5ca> Redefining land as floor space is the future. Somebody contact Urban dictionary. </p><br data-v-5fc4d5ca><p data-v-5fc4d5ca><strong data-v-5fc4d5ca>4. Money</strong></p><br data-v-5fc4d5ca><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> Do not say Bitcoin. That worthless thing one cannot even touch. </p><p data-v-5fc4d5ca>I mean, people being their own banks??</p><p data-v-5fc4d5ca>Haha. It will never work.</p><p data-v-5fc4d5ca>Too complex.</p><p data-v-5fc4d5ca>Too polluting.</p><p data-v-5fc4d5ca>Too pyramid schemey (Oops. Sorry).</p><br data-v-5fc4d5ca><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> Bitcoin.</p><p data-v-5fc4d5ca> Because with Bitcoin, nothing else needs to store economic value by registering a price increase. </p><p data-v-5fc4d5ca> Everything else gets cheaper, while Bitcoin price-value rockets up to the moon. Then to Mars. </p><p data-v-5fc4d5ca>The balance sheet is indeed finally balanced forever.</p><p data-v-5fc4d5ca>Asset (Store of Value) = Bitcoin.</p><p data-v-5fc4d5ca> Liabilities (Goods and Services that provide value to Assets) = Everything else. </p><p data-v-5fc4d5ca>&gt;&gt;&gt;infinity/21M&lt;&lt;&lt;</p><p data-v-5fc4d5ca>What a meme.</p><br data-v-5fc4d5ca><p data-v-5fc4d5ca><strong data-v-5fc4d5ca>5. *Energy*</strong></p><br data-v-5fc4d5ca><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> DS get over here</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> Yes.</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> You know it is not good for individuals to run their own energy power plants. You know this right? </p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> Says who?</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> Says common sense.</p><p data-v-5fc4d5ca>(They look at each other while their eyes say &#39;Chernobyl&#39; )</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> Ok. So who should run it.</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> Government of course.</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> But governments believe in making a budget after spending. They are wasteful. </p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> They can learn. Be patient.</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> When exactly will they learn that energy is the King resource and it should not be messed with? </p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> No choice. They must control the energy.</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> Oh but there is a choice. More efficiency minded individuals can group together into a successful legal entity called a corporation.You&#39;ve heard of it. </p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> Still under government control.</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> Yes and No.</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> Talk about the Yes.</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> Only as long as the government is a functioning entity.</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> Else?</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> The DS believers will be voted into the governments of the future. And they are growing and you know it. </p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>IS:</em> Well...</p><p data-v-5fc4d5ca><em data-v-5fc4d5ca>DS:</em> Well?</p><p style=\"text-align:center;\" data-v-5fc4d5ca>* * *</p><p data-v-5fc4d5ca>Inspiration of note - &#39;The Price of Tomorrow&#39; by Jeff Booth</p>", 91)
